.hue-range-slider:hover + .hue-range-output, .hue-range-slider:focus + .hue-range-output, .hue-range-slider:active + .hue-range-output {
  opacity: 1;
}

.hue-range-output {
  opacity: 0;
  position: absolute;
  left: calc(var(--hue) / 360 * 100%);
  margin-left: -9px;
  transform: translate(calc(var(--hue) / 360 * -50%), -5px);
  width: 36px;
  bottom: 100%;
  padding: 4px;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  border-radius: 12px;
  background-color: #343a40;
  color: white;
  transition: opacity 0.6s;
}
.hue-range-output::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -4px;
  left: 50%;
  margin-left: -4px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: #343a40 transparent transparent transparent;
}

